import { Component, h, Host, Prop, Element } from '@stencil/core';
import { TIconSize, cleanSVG, getSvgContent } from '../dc-ui-icon/icon-utils';
import { injectCSS } from '../../utils';
import { createStyles } from './progress-icon-styles';

@Component({
  tag: 'dc-ui-progress-icon',
  shadow: true,
})
export class DcUiProgressIcon {
  @Element() public host!: HTMLElement;
  @Prop() size: TIconSize = 'medium';
  @Prop() message: string;

  private ICONS_BASE_URL = 'https://dc-webtoolkit.azureedge.net/icons/';
  private name = 'dc_progress-icon';
  key = 0;
  svgContent = '';

  private loadIcon(): void {
    const url = `${this.ICONS_BASE_URL}${this.name}.svg`;
    getSvgContent(url).then((svgContent) => {
      this.setIconContent(svgContent);
    });
  }

  private setIconContent(content: string): void {
    this.svgContent = cleanSVG(content);
    const cleanContent = cleanSVG(content);

    const el = this.host.shadowRoot.querySelector('i');
    if (el) {
      el.innerHTML = cleanContent;
    }
  }

  public componentWillUpdate(): void {
    // reset old icon if there is any
    if (this.svgContent) {
      this.setIconContent('');
    }
  }

  async componentWillLoad() {
    this.loadIcon();
  }

  render() {
    injectCSS(this.host, createStyles, this.size);

    return (
      <Host>
        <div class="content-wrapper">
          <i key={this.key++} />
          {this.message && <dc-ui-text variant="strong-mid-size-text">{this.message}</dc-ui-text>}
        </div>
      </Host>
    );
  }
}
