import { getCss, mergeDeep, buildResponsiveStyles } from '../../utils';
import { ProgressIconSize, TProgressIconSize, PROGRESS_ICON_SIZE } from './progress-icon-utils';

export const createStyles = (size: TProgressIconSize): string => {
  const styles = getCss({
    '@global': {
      ':host': {
        display: 'inline-flex',
        color: 'inherit',
      },
      '.content-wrapper': {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'center',
        gap: '8px',
      },
      i: {
        display: 'flex',
        color: 'inherit',
        ...mergeDeep(
          buildResponsiveStyles(size, (sizeResponsive: ProgressIconSize) => ({
            width: `${PROGRESS_ICON_SIZE[sizeResponsive]}px`,
            height: `${PROGRESS_ICON_SIZE[sizeResponsive]}px`,
          }))
        ),
      },
      svg: {
        width: '100%',
        height: '100%',
      },
    },
  });

  return styles;
};
